import axios from "axios"
export default {
    namespaced: true,
    state: {
        roster: [],
        selected: []
    },
    getters: {
        roster: state => { return state.roster },
        selected: state => { return state.selected }  
    },
    mutations: {
        create(state, roster) {
            state.roster = roster
        },
        select(state, key){
            var size = state.selected.length;
            if(size < 3) state.selected.push(state.roster.slice(key, key+1)[0]) 
        },
        remove(state, key){
            console.log(state.selected[key].name);
            console.log(state.selected[key].profile);
        }
    },
    actions: {
        fetch_all(context){
            axios.get("/character")
                .then(res => {
                    context.commit("create", res.data.roster)
                })
        },
        select_char(context, key){
            context.commit("select", key)
        },
        remove_char(context, key){
            context.commit("remove", key)
        }
    }
}