<template>
  <section>
    <div class="team_view">
      <img
        v-for="(char, key) in selected"
        @dblclick="remove_char(key)"
        :key="key"
        class="icon icon--small icon--round team_view__char"
        :src="'https://smasharena.herokuapp.com/character/profile/'+char.id+'.jpg'"
        :alt="char.name"
      />
    </div>
    <button @click="start_game">Start game</button>
  </section>
</template>

<script>
import axios from "axios";
export default {
  computed: {
    selected() {
      return this.$store.getters["character/selected"];
    }
  },
  methods: {
    remove_char(key) {
      this.$store.dispatch("character/remove_char", key);
    },
    start_game() {
      axios
        .post("/newgame", { userID: "sans", teamID: ["1", "2", "3"] })
        .then(res => {
          if(res.data){
            this.$router.push('arena')
          }
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
.team_view {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  height: 30%;
  background-color: black;
  &__char {
    margin: 0.5rem;
  }
}
</style>